.banner-box {
    background: #30323C;
    background-repeat: no-repeat;
    background-position-x: center;
    width: 100vw;
    min-width: 1080px;
    height: 7rem;
    overflow: hidden;
    .content-box {
        width: 11.6rem;
        margin: 0 auto;
        padding-top: .42rem;
        .title-box {
            font-size: .4rem;
            color: #ffffff;
            width: 3.82rem;
            height: .97rem;            
            text-align: center;
            margin: 0 auto;
        }
        .desc-box {
            width: 2.64rem;
            // height: .32rem;
            margin: 0 auto;
            font-size: .12rem;
            font-weight: 100;
            line-height: .18rem;
            color: #FFFFFF;
            padding-top: .07rem;
            opacity: 0.55;   
            text-align: center;         
        }
        .btn-box {
            width: 2.24rem;
            height: .44rem;
            background: #3F456B;
            opacity: 1;            
            font-size: .16rem;
            color: #ffffff;
            display: flex;
            margin: 0 auto;
            margin: .27rem auto .24rem;
            .btn-title-box {
                text-align: center;
                width: 1.7rem;
                height: .44rem;
                line-height: .44rem;
                border-right: 1px solid #2F313B;
            }
            .icon-box {
                display: flex;
                align-items: center;
                justify-content: center;
                width: .53rem;
            }
        }
        .code-img-box {
            width: 8.98rem;
            height: 4.25rem;
            background: #FFFFFF;
            opacity: 1;   
            margin: 0 auto;
            border-radius: 8px 8px 0 0;
            img {
                width: 100%;
            }         
        }
    }
}