.func-box {
    width: 100vw;
    min-width: 1080px;
    height: 1.54rem;
    background: #FFFFFF;
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.02);
    opacity: 1;    
    .item-list {
        display: flex;
        justify-content: space-around;
        width: 11.6rem;
        height: 100%;
        margin: 0 auto;
        a {
            padding: 0 .4rem;
            .item-list-box {
                position: relative;
                .list-icon-box {
                    position: relative;
                    bottom: .15rem;
                    text-align: center;
                }
                .title-box {
                    position: relative;
                    bottom: .15rem;                
                    font-size: .18rem;
                    font-weight: 300;
                    color: #30323C;
                    opacity: 1;     
                    text-align: center;           
                }
            }
        }
        a:hover {
            background: #F5F8FB;
        }
    }
}

.introduce-box {
    width: 100vw;
    min-width: 1080px;
    .item-list {
        margin: 0 auto;
        .list {
            width: 100vw;
            min-width: 1080px;
            .item-list-box {
                display: flex;
                height: 3.9rem;
                width: 11.6rem;
                margin: 0 auto;
                position: relative;
                .list-img-box {
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    img {
                        width: 3.78rem;
                        height: auto;
                    }
                }
                .list-content-box {
                    .title-box {
                        font-size: .28rem;
                        color: #333333;
                        padding: 1.14rem 0px .1rem 0px;
                    }
                    .desc-box {
                        font-size: .16rem;
                        color: #333333;
                    }
                }
            }
        }
        .list:nth-child(odd) {
            background: #F5F8FB;
        }
        .list:nth-child(even) {
            background: #FFFFFF;
        }
    }
}