.download-box {
    width: 100vw;
    min-width: 1080px;
    height: 3.9rem;
    background: #f5f5f5;
    padding: .28rem 0 .78rem 0;
    .title-box {
        height: .34rem;
        font-size: .28rem;
        font-weight: 400;
        line-height: .34rem;
        color: #333333;
        text-align: center;
        margin-bottom: .68rem;
    }
    .content-box {
        .list-box {
            display: flex;
            width: 11.6rem;
            margin: 0 auto;
            justify-content: space-around;
            .list-content {
                display: flex;
                flex-direction: column;
                .icon-box {
                    text-align: center;
                }
                .btn-box {
                    background: #3F456B;
                    width: 2rem;
                    height: .54rem;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin-top: .44rem;
                    .title {
                        font-size: .16rem;
                        text-align: center;     
                        color: #FFFFFF;
                    }
                }
            }
        }
    }
}