.bottom-box {
    display: flex;
    align-items: center;
    width: 100vw;
    min-width: 1080px;
    height: .78rem;
    background: #353742;
    .content-box {
        display: flex;
        width: 11.6rem;
        margin: 0 auto;
        justify-content: flex-start;
        img {
            padding-bottom: 2px;
            margin-right: .2rem;
        }
    }
    .copy-right-box {
        height: .78rem;
        line-height: .78rem;
        font-size: .12rem;
        position: absolute;
        right: 3.8rem;
        color: #C6C8D1;
        opacity: .55;
    }
}