.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.icon {
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}


html, body {
  min-width: 1080px;
}

a:link {text-decoration: none; color: #C6C8D1;}
a:active { color: #C6C8D1; }
a:visited {text-decoration: none; color: #C6C8D1;} 
a:hover { text-decoration: none; color: #C6C8D1;} 


ul,li {
  list-style: none;
  padding: 0px;
  margin: 0px;
}
