.nav-box {
    background: #353742;
    height: .49rem;
    position: sticky;
    top: 0;    
    z-index: 999;
    .content-box {
        width: 11.6rem;
        height: 100%;
        margin: 0 auto;
        .nav-list-box {  
            display: flex;   
            justify-content: space-between;
            img {
                position: relative;
                // left: -1.8rem;
            }
        }
    }
    .nav-download-box {
        display: flex;
        align-content: center;
        justify-content: center;
        text-align: center;
        width: 1.3rem;
        height: .49rem;
        line-height: .49rem;
        position: absolute;
        right: 2.2rem;
        top: 0;
        color: #ffffff;
        font-size: .15rem;
        background: #3F456B;
    }
}


.nav-ul-box {
    display: flex;
    justify-content: space-between;
    height: 100%;
    .li-box {
        display: flex;
        align-items: center;
        height: 100%;
        // width: .54rem;
        box-sizing: border-box;
        font-size: .15rem;
        color: #C6C8D1;
        a {
            display: inline-block;
            height: .49rem;
            line-height: .49rem;
            padding: 0 0.12rem;
        }
    }
    .li-box:hover {
        a {
            color: #ffffff;
        }
    }
}